import { Box, Button, FormControl, Input, Modal, ModalBody, ModalCloseButton, ModalContent, ModalFooter, ModalHeader, ModalOverlay, useDisclosure, useToast } from '@chakra-ui/react'
import React, { useState } from 'react'
import { ChatState } from '../../Context/ChatProvider';
import axios from 'axios';
import UserListItem from '../UserAvatar/UserListItem';
import UserBadgeItem from '../UserAvatar/UserBadgeItem';

const GroupChatModal = ({children}) => {
    const { isOpen, onOpen, onClose } = useDisclosure();
    const [groupChatName, setGroupChatName] = useState();
    const [selectedUsers, setSelectedUsers] = useState([]);
    const [search, setSearch] = useState("");
    const [searchResult, setSearchResult] = useState([]);
    const [loading, setLoading] = useState(false);
    const [pic, setPic] = useState();

    const toast = useToast();

    const {user, chats, setChats } = ChatState();

    const postDetails = (pics) => {
        setLoading(true);
        if(pics === undefined){
            toast({
                title: 'Please Select an Image!',
                status:'warning',
                duration: 5000,
                isClosable: true,
                position: 'bottom',
            })
            return;
        }
        if(pics.type==='image/jpeg' || pics.type === 'image/png'){
            const data = new FormData();
            data.append('file',pics);
            data.append('upload_preset', "ChatHub");
            data.append('cloud_name', 'geekypartha');
            fetch('https://api.cloudinary.com/v1_1/geekypartha/image/upload',{
                method:'post',
                body: data,
            }).then((res) => res.json())
            .then(data=>{
                setPic(data.url.toString());
                console.log(data.url.toString());
                setLoading(false);
            })
            .catch((err)=>{
                console.log(err);
                setLoading(false);
            });
        }else{
            toast({
                title: 'Please Select an Image!',
                status: 'warning',
                duration: 5000,
                isClosable: true,
            })
            setLoading(false);
            return;
        }
    };

    const handleSearch=async(query)=>{
        setSearch(query);
        if(!query){
            return;
        }
        try {
            setLoading(true);
            const config = {
                headers: {
                    Authorization: `Bearer ${user.token}`,
                },
            };
            const {data} = await axios.get(`/api/user?search=${search}`, config);
            // console.log(data);
            setLoading(false);
            setSearchResult(data);
        } catch (error) {
            toast({
                title: "Error Occured",
                description: "Failed to Load the Search Results",
                status: "error",
                duration:5000,
                isClosable: true,
                position: "bottom-left",
      
            });
        }
    }
    
    const handleGroup=(userToAdd)=>{
        if(selectedUsers.includes(userToAdd)){
            toast({
                title: "User already added",
                status: "warning",
                fontFamily: "twitterchirp",
                duration:5000,
                isClosable: true,
                position: "top",
      
            });
            return;
        }
        setSelectedUsers([...selectedUsers, userToAdd]);
    };

    const handleDelete =(delUser)=>{
        setSelectedUsers(selectedUsers.filter((sel) => sel._id!== delUser._id))
    }

    const handleSubmit= async()=>{
        if(!groupChatName || !selectedUsers){
            toast({
                title: "Please fill all the fields",
                status: "warning",
                fontFamily: "twitterchirp",
                duration:5000,
                isClosable: true,
                position: "top",
      
            });
            return;
        }
        try {
            const config = {
                headers: {
                    Authorization: `Bearer ${user.token}`,
                },
            };
            const {data} = await axios.post("/api/chat/group",
                {
                    name: groupChatName,
                    users:JSON.stringify(selectedUsers.map((u)=> u._id)),
                },
            config
            );
            setChats([data,...chats]);
            onClose();
            toast({
                title: "New Group Chat Created!",
                status: "success",
                fontFamily: "twitterchirp",
                duration:5000,
                isClosable: true,
                position: "bottom",
      
            });
        } catch (error) {
            toast({
                title: "Failed to Create the Chat",
                description: error.response.data,
                status: "error",
                fontFamily: "twitterchirp",
                duration:5000,
                isClosable: true,
                position: "top",
      
            });
        }
    };
    
    return (
        <>
        <span onClick={onOpen}>{children}</span>

        <Modal isOpen={isOpen} onClose={onClose}>
            <ModalOverlay/>
            <ModalContent>
            <ModalHeader
                fontSize="30px"
                fontWeight={'400'}
                fontFamily="Work sans"
                display="flex"
                justifyContent="center"
                alignItems={'center'}
            >Create Group</ModalHeader>
            <ModalCloseButton />
            <ModalBody
                display="flex" flexDir="column" alignItems="center"
            >
                <FormControl>
                    <Input placeholder='Chat Name' mb={3} fontFamily={"twitterchirp"}
                    onChange={(e)=> setGroupChatName(e.target.value)}
                    />
                </FormControl>
                <FormControl>
                    <Input placeholder="Add Users eg: Partha" mb={1} fontFamily={"twitterchirp"}
                    onChange={(e)=> handleSearch(e.target.value)}
                    />
                </FormControl>
                <FormControl>
                    <Input
                        type={'file'}
                        fontFamily={"twitterchirp"}
                        p={'1'}
                        accept='image/*'
                        onChange={(e)=>postDetails(e.target.files[0])}
                    />
                </FormControl>

                <Box width={'100%'} display={'flex'} flexWrap={'wrap'}>
                {selectedUsers.map(u=>(
                    <UserBadgeItem key ={user._id} user={u}
                    handleFunction={()=>handleDelete(u)}
                    />
                ))}
                </Box>
                
                {loading? (
                    <div>loading</div>
                ):(
                    searchResult
                    ?.slice(0,4)
                    .map((user)=>(
                        <UserListItem 
                        key={user._id} 
                        user={user} 
                        handleFunction={()=>handleGroup(user)}
                        />
                    ))
                )}
            </ModalBody>

            <ModalFooter>
                <Button colorScheme='blue'onClick={handleSubmit} fontFamily={"twitterchirp"}>
                Create Chat
                </Button>
                
            </ModalFooter>
            </ModalContent>
        </Modal>
        </>
    )
}

export default GroupChatModal